body {
  background-color: #eceff1;
  color: #4d4d4d;
  font-family: 'Lato', sans-serif;
}

.Container {
  max-width: 1024px;
  margin: 0 auto 0 auto;
  /* padding: 0 14px 0 14px; */
}

.row {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (min-width: 768px){
  
}
.Header {
  max-width: 100%;
  background-color: #1abc9c;
  border-radius: 10px;
  box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.5);
}

.Header-row {
  display: flex;
  flex-wrap: wrap;
}

.Header-Logo {
  width: 20%;
}

.Header-Logo img {
  width: 100%;
}

.Header-Search {
  width: 80%;
  float: right;
}

.Header-Search h1 {
  float: right;
}
.poke-row {
  width: 100%;
  display:flex;
  flex-wrap: wrap;
}


.PokeCard {
  /* width: 33.333%; */
  /* padding: 14px; */
  -webkit-transition: all ease 500ms;
  transition: all ease 500ms;
  box-sizing: border-box;
}

.PokeCard-Item {
  height: 220px;
  border-radius: 5%;
  color: #fff;
  background-color: #00a8cc;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-decoration: none;
  -webkit-transition: all ease 500ms;
  transition: all ease 500ms;
}

.PokeCard:hover {
  -webkit-transform: scale(1.10);
          transform: scale(1.10);
  opacity: 0.7;
  cursor: pointer;
}

.PokeCard-Title {
  position: relative;
  left: 12px;
  top: 36px;
  font-size: 26px;
}

.PokeCard img {
  width: 153px;
  position: relative;
  bottom: 51px;
}

/* .PokeCard img:hover {
  transform: scale(1.25)
} */

.PokeCard-Desc {
  position: relative;
  top: 10px;
  left: 12px;
  font-size: 21px;
}

.Fire {
  background-color: #fb8d62;
}

.Water {
  background-color: #3498db;
}

.Grass {
  background-color: #27ae60
}

.Bug {
  background-color: #c9753d
}

.Fairy {
  background-color: #e84393
}

.Flying {
  background-color: #a7e9af
}

.Electric {
  background-color: #f39c12
}

.Card-Thumbnail img {
  border-radius: 100%;
  width: 50%;
  background: #333;
}

.Card {
  width: 33.333%;
  padding: 19px;
  box-sizing: border-box;
}

.Card-Item {
  background: white;
  box-shadow:  0px 10px 14px rgba(0, 0, 0, 0.25);
  height: 64vh;
  min-height: 300px;
  max-height: 300px;
}

.Card-Body {
  padding: 14px;
  text-align: center;
}

.Card-Body span {
  background: aquamarine;
  padding: 5px 5px;
  font-size: 12px;
  border-radius: 3px;
}

.Card-Thumbnail {
  position: relative;
  text-align: center;
  border-radius: 100%;
  padding: 21px 18px;
}

.Card-Body h2 {
  line-height: 0.3;
}
.PokeDex-Cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Pokedex h2 {
  margin-bottom: 0;
}

.Pokedex-Search {
 width: 100%;
 margin-right: 12px;
}

.search-container {
  margin-top: 22px;
  width: 100%;
  text-align: center;
}

input[type=text] {
  padding: 6px;
  margin-top: 8px;
  font-size: 17px;
  border: none;
}

.search-container button {
  float: right;
  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.search-container button:hover {
  background: #ccc;
}
.DetailPoke-Title {
  text-align: center;
  font-size: larger;
}

.DetailPoke-image {
  padding: 38px;
  border-radius: 5px;
}

.DetailPoke-image img {
  display: block;
  margin: auto;
  width: 70%;
}

.row {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    margin-top: 20px;
}

.col1 {
  width: 50%;
  padding: 14px;
  box-sizing: border-box;
}

.DetailPoke-Desc {
  -webkit-user-select: all;
     -moz-user-select: all;
      -ms-user-select: all;
          user-select: all;
}

.itemLo {
  box-shadow: 0 2px 15px 0.5px rgba(0,0,0,0.1);
  padding: 14px;
  border-radius: 3px;
  background: #fff;
  padding-bottom: 10%;
  overflow: hidden;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}

.DetailPoke-Title h2 {
  margin-bottom: auto;
}

.DetailPoke-Title span {
  color: #ff5151;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 16px;
}

.DetailPoke-Desc p {
  line-height: 1.5;
  font-size: 14px;
}


